import React from 'react';
import {
  Box,
  Typography,
  Paper,
  Avatar,
  Chip,
  CircularProgress,
  useTheme,
} from '@mui/material';
import { Person, SmartToy } from '@mui/icons-material';
import { ChatMessage as ChatMessageType } from '../../services/chat/websocketService';
import { format } from 'date-fns';

interface ChatMessageProps {
  message: ChatMessageType;
  isGenerating?: boolean;
}

export const ChatMessage: React.FC<ChatMessageProps> = ({ message, isGenerating }) => {
  const theme = useTheme();
  const isUser = message.isUser;

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: isUser ? 'row-reverse' : 'row',
        mb: 2,
        alignItems: 'flex-start',
      }}
    >
      <Avatar
        sx={{
          bgcolor: isUser ? theme.palette.primary.main : theme.palette.secondary.main,
          mx: 1,
          width: 32,
          height: 32,
        }}
      >
        {isUser ? <Person fontSize="small" /> : <SmartToy fontSize="small" />}
      </Avatar>
      
      <Box sx={{ maxWidth: '70%', minWidth: '200px' }}>
        <Paper
          elevation={1}
          sx={{
            p: 2,
            bgcolor: isUser ? theme.palette.primary.light : theme.palette.grey[100],
            color: isUser ? theme.palette.primary.contrastText : theme.palette.text.primary,
            borderRadius: 2,
            position: 'relative',
          }}
        >
          <Typography
            variant="body1"
            sx={{
              whiteSpace: 'pre-wrap',
              wordBreak: 'break-word',
              fontFamily: isUser ? 'inherit' : 'monospace',
              fontSize: isUser ? '0.875rem' : '0.8rem',
            }}
          >
            {message.content}
          </Typography>
          
          {isGenerating && !message.isComplete && (
            <Box
              sx={{
                display: 'flex',
                alignItems: 'center',
                mt: 1,
                opacity: 0.7,
              }}
            >
              <CircularProgress size={12} sx={{ mr: 1 }} />
              <Typography variant="caption">Generating...</Typography>
            </Box>
          )}
        </Paper>
        
        <Box
          sx={{
            display: 'flex',
            justifyContent: isUser ? 'flex-end' : 'flex-start',
            mt: 0.5,
            alignItems: 'center',
          }}
        >
          <Typography
            variant="caption"
            color="text.secondary"
            sx={{ mr: 1 }}
          >
            {format(message.timestamp, 'HH:mm')}
          </Typography>
          
          {!message.isComplete && !isUser && (
            <Chip
              label="Streaming"
              size="small"
              color="primary"
              variant="outlined"
              sx={{ height: 16, fontSize: '0.6rem' }}
            />
          )}
        </Box>
      </Box>
    </Box>
  );
};
