export interface ChatMessage {
  id: string;
  content: string;
  timestamp: Date;
  isUser: boolean;
  isComplete: boolean;
}

export interface ChatStreamEvent {
  type: 'token' | 'complete' | 'error';
  data: string;
  messageId?: string;
}

export interface ProjectMemory {
  id: string;
  key: string;
  value: any;
  timestamp: Date;
  scope: string;
}

export class ChatWebSocketService {
  private ws: WebSocket | null = null;
  private reconnectAttempts = 0;
  private maxReconnectAttempts = 5;
  private reconnectDelay = 1000;
  private messageQueue: string[] = [];
  private isConnecting = false;

  // Event callbacks
  private onMessageCallback: ((message: ChatMessage) => void) | null = null;
  private onStreamEventCallback: ((event: ChatStreamEvent) => void) | null = null;
  private onConnectionStatusCallback: ((status: 'connected' | 'disconnected' | 'connecting' | 'error') => void) | null = null;
  private onMemoryUpdateCallback: ((memory: ProjectMemory[]) => void) | null = null;

  constructor(private wsUrl: string) {}

  connect(): Promise<void> {
    if (this.isConnecting || (this.ws && this.ws.readyState === WebSocket.OPEN)) {
      return Promise.resolve();
    }

    this.isConnecting = true;
    this.onConnectionStatusCallback?.('connecting');

    return new Promise((resolve, reject) => {
      try {
        this.ws = new WebSocket(this.wsUrl);

        this.ws.onopen = () => {
          console.log('WebSocket connected to Chat Orchestrator');
          this.isConnecting = false;
          this.reconnectAttempts = 0;
          this.onConnectionStatusCallback?.('connected');
          
          // Send any queued messages
          while (this.messageQueue.length > 0) {
            const message = this.messageQueue.shift();
            if (message) {
              this.ws?.send(message);
            }
          }
          
          resolve();
        };

        this.ws.onmessage = (event) => {
          try {
            const data = JSON.parse(event.data);
            this.handleMessage(data);
          } catch (error) {
            console.error('Error parsing WebSocket message:', error);
          }
        };

        this.ws.onclose = () => {
          console.log('WebSocket disconnected');
          this.isConnecting = false;
          this.onConnectionStatusCallback?.('disconnected');
          this.attemptReconnect();
        };

        this.ws.onerror = (error) => {
          console.error('WebSocket error:', error);
          this.isConnecting = false;
          this.onConnectionStatusCallback?.('error');
          reject(error);
        };
      } catch (error) {
        this.isConnecting = false;
        this.onConnectionStatusCallback?.('error');
        reject(error);
      }
    });
  }

  private handleMessage(data: any) {
    switch (data.type) {
      case 'chat_message':
        this.onMessageCallback?.(data.message);
        break;
      case 'stream_event':
        this.onStreamEventCallback?.(data.event);
        break;
      case 'memory_update':
        this.onMemoryUpdateCallback?.(data.memory);
        break;
      default:
        console.warn('Unknown message type:', data.type);
    }
  }

  private attemptReconnect() {
    if (this.reconnectAttempts < this.maxReconnectAttempts) {
      this.reconnectAttempts++;
      console.log(`Attempting to reconnect (${this.reconnectAttempts}/${this.maxReconnectAttempts})`);
      
      setTimeout(() => {
        this.connect().catch(console.error);
      }, this.reconnectDelay * Math.pow(2, this.reconnectAttempts - 1));
    }
  }

  sendMessage(content: string): void {
    const message = JSON.stringify({
      type: 'user_message',
      content: content,
      timestamp: new Date().toISOString()
    });

    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(message);
    } else {
      // Queue message for when connection is restored
      this.messageQueue.push(message);
      if (!this.isConnecting) {
        this.connect().catch(console.error);
      }
    }
  }

  stopGeneration(): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify({
        type: 'stop_generation'
      }));
    }
  }

  requestMemorySnapshot(): void {
    if (this.ws && this.ws.readyState === WebSocket.OPEN) {
      this.ws.send(JSON.stringify({
        type: 'get_memory'
      }));
    }
  }

  disconnect(): void {
    if (this.ws) {
      this.ws.close();
      this.ws = null;
    }
  }

  // Event subscription methods
  onMessage(callback: (message: ChatMessage) => void): void {
    this.onMessageCallback = callback;
  }

  onStreamEvent(callback: (event: ChatStreamEvent) => void): void {
    this.onStreamEventCallback = callback;
  }

  onConnectionStatus(callback: (status: 'connected' | 'disconnected' | 'connecting' | 'error') => void): void {
    this.onConnectionStatusCallback = callback;
  }

  onMemoryUpdate(callback: (memory: ProjectMemory[]) => void): void {
    this.onMemoryUpdateCallback = callback;
  }

  isConnected(): boolean {
    return this.ws?.readyState === WebSocket.OPEN;
  }
}
