import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Paper,
  Accordion,
  AccordionSummary,
  AccordionDetails,
  Chip,
  IconButton,
  TextField,
  InputAdornment,
  List,
  ListItem,
  ListItemText,
  Divider,
  Button,
  useTheme,
} from '@mui/material';
import {
  ExpandMore,
  Memory,
  Search,
  Refresh,
  FilterList,
} from '@mui/icons-material';
import { useChat } from '../../contexts/ChatContext';
import { ProjectMemory } from '../../services/chat/websocketService';
import { format } from 'date-fns';

export const MemoryInspector: React.FC = () => {
  const [expanded, setExpanded] = useState(false);
  const [searchTerm, setSearchTerm] = useState('');
  const [scopeFilter, setScopeFilter] = useState<string>('all');
  const theme = useTheme();
  const { projectMemory, requestMemorySnapshot } = useChat();

  const handleAccordionChange = (_: React.SyntheticEvent, isExpanded: boolean) => {
    setExpanded(isExpanded);
    if (isExpanded) {
      requestMemorySnapshot();
    }
  };

  const handleRefresh = () => {
    requestMemorySnapshot();
  };

  // Filter memory items based on search and scope
  const filteredMemory = projectMemory.filter(item => {
    const matchesSearch = searchTerm === '' || 
      item.key.toLowerCase().includes(searchTerm.toLowerCase()) ||
      JSON.stringify(item.value).toLowerCase().includes(searchTerm.toLowerCase());
    
    const matchesScope = scopeFilter === 'all' || item.scope === scopeFilter;
    
    return matchesSearch && matchesScope;
  });

  // Get unique scopes for filtering
  const uniqueScopes = Array.from(new Set(projectMemory.map(item => item.scope)));

  const renderMemoryValue = (value: any): string => {
    if (typeof value === 'string') {
      return value;
    }
    return JSON.stringify(value, null, 2);
  };

  const getValuePreview = (value: any): string => {
    const str = renderMemoryValue(value);
    return str.length > 100 ? str.substring(0, 100) + '...' : str;
  };

  return (
    <Paper
      elevation={1}
      sx={{
        bgcolor: theme.palette.background.paper,
        border: `1px solid ${theme.palette.divider}`,
      }}
    >
      <Accordion
        expanded={expanded}
        onChange={handleAccordionChange}
        sx={{
          boxShadow: 'none',
          '&:before': {
            display: 'none',
          },
        }}
      >
        <AccordionSummary
          expandIcon={<ExpandMore />}
          sx={{
            bgcolor: theme.palette.primary.light + '10',
            '&:hover': {
              bgcolor: theme.palette.primary.light + '20',
            },
          }}
        >
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
            <Memory color="primary" />
            <Typography variant="h6">
              Project Memory Inspector
            </Typography>
            <Chip
              label={projectMemory.length}
              size="small"
              color="primary"
              variant="outlined"
            />
          </Box>
        </AccordionSummary>
        
        <AccordionDetails sx={{ p: 0 }}>
          <Box sx={{ p: 2 }}>
            {/* Search and Filter Controls */}
            <Box sx={{ display: 'flex', gap: 2, mb: 2, alignItems: 'center' }}>
              <TextField
                size="small"
                placeholder="Search memory keys and values..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Search />
                    </InputAdornment>
                  ),
                }}
                sx={{ flexGrow: 1 }}
              />
              
              <Button
                variant="outlined"
                startIcon={<FilterList />}
                onClick={() => setScopeFilter(scopeFilter === 'all' ? (uniqueScopes[0] || 'all') : 'all')}
                size="small"
              >
                {scopeFilter === 'all' ? 'All Scopes' : scopeFilter}
              </Button>
              
              <IconButton
                onClick={handleRefresh}
                color="primary"
                size="small"
                title="Refresh memory"
              >
                <Refresh />
              </IconButton>
            </Box>
            
            {/* Memory List */}
            {filteredMemory.length === 0 ? (
              <Typography
                variant="body2"
                color="text.secondary"
                sx={{ textAlign: 'center', py: 4 }}
              >
                {projectMemory.length === 0 
                  ? 'No memory items found. Start a conversation to see project memory.'
                  : 'No memory items match your search criteria.'
                }
              </Typography>
            ) : (
              <List dense>
                {filteredMemory.map((item, index) => (
                  <React.Fragment key={item.id}>
                    <ListItem
                      sx={{
                        flexDirection: 'column',
                        alignItems: 'flex-start',
                        py: 2,
                      }}
                    >
                      <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mb: 1, width: '100%' }}>
                        <Typography variant="subtitle2" sx={{ fontWeight: 600 }}>
                          {item.key}
                        </Typography>
                        <Chip
                          label={item.scope}
                          size="small"
                          variant="outlined"
                          sx={{ ml: 'auto' }}
                        />
                      </Box>
                      
                      <Typography
                        variant="body2"
                        color="text.secondary"
                        sx={{
                          fontFamily: 'monospace',
                          fontSize: '0.75rem',
                          bgcolor: theme.palette.grey[50],
                          p: 1,
                          borderRadius: 1,
                          width: '100%',
                          whiteSpace: 'pre-wrap',
                          wordBreak: 'break-word',
                        }}
                      >
                        {getValuePreview(item.value)}
                      </Typography>
                      
                      <Typography variant="caption" color="text.secondary" sx={{ mt: 0.5 }}>
                        Updated: {format(item.timestamp, 'PPpp')}
                      </Typography>
                    </ListItem>
                    
                    {index < filteredMemory.length - 1 && <Divider />}
                  </React.Fragment>
                ))}
              </List>
            )}
          </Box>
        </AccordionDetails>
      </Accordion>
    </Paper>
  );
};
