import React, { useEffect, useRef } from 'react';
import {
  Box,
  Typography,
  IconButton,
  Divider,
  useTheme,
  Paper,
  useMediaQuery,
} from '@mui/material';
import { Clear, SmartToy } from '@mui/icons-material';
import { useChat } from '../../contexts/ChatContext';
import { ChatMessage } from '../../components/chat/ChatMessage';
import { ChatInput } from '../../components/chat/ChatInput';
import { ConnectionStatus } from '../../components/chat/ConnectionStatus';
import { MemoryInspector } from '../../components/chat/MemoryInspector';

export const ChatPage: React.FC = () => {
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('md'));
  const { messages, clearMessages, isGenerating } = useChat();
  const messagesEndRef = useRef<HTMLDivElement>(null);

  // Auto-scroll to bottom when new messages arrive
  useEffect(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, [messages]);

  const handleClearMessages = () => {
    clearMessages();
  };

  return (
    <Box sx={{ height: '100vh', display: 'flex', flexDirection: 'column' }}>
      {/* Header */}
      <Box
        sx={{
          display: 'flex',
          alignItems: 'center',
          justifyContent: 'space-between',
          p: 2,
          bgcolor: theme.palette.background.paper,
          borderBottom: `1px solid ${theme.palette.divider}`,
        }}
      >
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2 }}>
          <SmartToy color="primary" />
          <Typography variant="h5" component="h1">
            AI Chat Interface
          </Typography>
        </Box>
        
        <IconButton
          onClick={handleClearMessages}
          color="secondary"
          title="Clear conversation"
          disabled={messages.length === 0}
        >
          <Clear />
        </IconButton>
      </Box>

      {/* Connection Status */}
      <ConnectionStatus />

      {/* Main Content Area */}
      <Box sx={{ display: 'flex', flexGrow: 1, overflow: 'hidden' }}>
        {/* Left Pane - Conversation History */}
        <Box
          sx={{
            width: isMobile ? '100%' : '70%',
            display: 'flex',
            flexDirection: 'column',
            overflow: 'hidden',
          }}
        >
          {/* Messages Container */}
          <Box
            sx={{
              flexGrow: 1,
              overflow: 'auto',
              p: 2,
              bgcolor: theme.palette.background.default,
            }}
          >
            {messages.length === 0 ? (
              <Box
                sx={{
                  display: 'flex',
                  flexDirection: 'column',
                  alignItems: 'center',
                  justifyContent: 'center',
                  height: '100%',
                  textAlign: 'center',
                  gap: 2,
                }}
              >
                <SmartToy sx={{ fontSize: 64, color: theme.palette.primary.main, opacity: 0.5 }} />
                <Typography variant="h6" color="text.secondary">
                  Welcome to Live AI Chat
                </Typography>
                <Typography variant="body2" color="text.secondary" sx={{ maxWidth: 400 }}>
                  Start a conversation with our AI consciousness. Your messages are streamed directly to the live model with no predefined responses.
                </Typography>
              </Box>
            ) : (
              <>
                {messages.map((message) => (
                  <ChatMessage
                    key={message.id}
                    message={message}
                    isGenerating={isGenerating && !message.isComplete}
                  />
                ))}
                <div ref={messagesEndRef} />
              </>
            )}
          </Box>

          {/* Chat Input */}
          <ChatInput />
        </Box>

        {/* Right Pane - Live Stream & Memory Inspector (Desktop Only) */}
        {!isMobile && (
          <>
            <Divider orientation="vertical" flexItem />
            <Box
              sx={{
                width: '30%',
                display: 'flex',
                flexDirection: 'column',
                overflow: 'hidden',
                bgcolor: theme.palette.background.paper,
              }}
            >
              {/* Live Stream Indicator */}
              <Paper
                elevation={0}
                sx={{
                  p: 2,
                  bgcolor: theme.palette.background.default,
                  borderBottom: `1px solid ${theme.palette.divider}`,
                }}
              >
                <Typography variant="h6" gutterBottom>
                  Live Stream
                </Typography>
                <Typography variant="body2" color="text.secondary">
                  {isGenerating 
                    ? 'AI is actively generating response...' 
                    : 'Ready for your next message'
                  }
                </Typography>
                
                {/* Stream visualization */}
                <Box
                  sx={{
                    mt: 2,
                    height: 4,
                    bgcolor: theme.palette.grey[200],
                    borderRadius: 2,
                    overflow: 'hidden',
                  }}
                >
                  <Box
                    sx={{
                      height: '100%',
                      bgcolor: isGenerating ? theme.palette.primary.main : theme.palette.grey[300],
                      borderRadius: 2,
                      animation: isGenerating ? 'pulse 1.5s ease-in-out infinite' : 'none',
                      '@keyframes pulse': {
                        '0%': { opacity: 0.6 },
                        '50%': { opacity: 1 },
                        '100%': { opacity: 0.6 },
                      },
                    }}
                  />
                </Box>
              </Paper>

              {/* Memory Inspector */}
              <Box sx={{ p: 2, overflow: 'auto' }}>
                <MemoryInspector />
              </Box>
            </Box>
          </>
        )}
      </Box>

      {/* Mobile Memory Inspector (Collapsible) */}
      {isMobile && (
        <Box sx={{ p: 2, bgcolor: theme.palette.background.paper }}>
          <MemoryInspector />
        </Box>
      )}
    </Box>
  );
};
