import React, { createContext, useContext, useEffect, useState, useCallback } from 'react';
import { ChatWebSocketService, ChatMessage, ChatStreamEvent, ProjectMemory } from '../services/chat/websocketService';

interface ChatContextType {
  messages: ChatMessage[];
  isConnected: boolean;
  isGenerating: boolean;
  connectionStatus: 'connected' | 'disconnected' | 'connecting' | 'error';
  projectMemory: ProjectMemory[];
  sendMessage: (content: string) => void;
  stopGeneration: () => void;
  clearMessages: () => void;
  requestMemorySnapshot: () => void;
}

const ChatContext = createContext<ChatContextType | undefined>(undefined);

interface ChatProviderProps {
  children: React.ReactNode;
}

export const ChatProvider: React.FC<ChatProviderProps> = ({ children }) => {
  const [messages, setMessages] = useState<ChatMessage[]>([]);
  const [isConnected, setIsConnected] = useState(false);
  const [isGenerating, setIsGenerating] = useState(false);
  const [connectionStatus, setConnectionStatus] = useState<'connected' | 'disconnected' | 'connecting' | 'error'>('disconnected');
  const [projectMemory, setProjectMemory] = useState<ProjectMemory[]>([]);
  const [wsService] = useState(() => {
    // Get WebSocket URL from environment or use default
    const wsUrl = process.env.REACT_APP_CHAT_WS_URL || 'ws://localhost:8080/chat';
    return new ChatWebSocketService(wsUrl);
  });

  useEffect(() => {
    // Set up WebSocket event handlers
    wsService.onConnectionStatus((status) => {
      setConnectionStatus(status);
      setIsConnected(status === 'connected');
    });

    wsService.onMessage((message) => {
      setMessages(prev => [...prev, message]);
    });

    wsService.onStreamEvent((event) => {
      switch (event.type) {
        case 'token':
          // Update the last message with the new token
          setMessages(prev => {
            const newMessages = [...prev];
            const lastMessage = newMessages[newMessages.length - 1];
            
            if (lastMessage && !lastMessage.isUser && !lastMessage.isComplete) {
              lastMessage.content += event.data;
            } else {
              // Create new AI message if none exists
              newMessages.push({
                id: event.messageId || Date.now().toString(),
                content: event.data,
                timestamp: new Date(),
                isUser: false,
                isComplete: false
              });
            }
            return newMessages;
          });
          setIsGenerating(true);
          break;
          
        case 'complete':
          // Mark the last message as complete
          setMessages(prev => {
            const newMessages = [...prev];
            const lastMessage = newMessages[newMessages.length - 1];
            if (lastMessage && !lastMessage.isUser) {
              lastMessage.isComplete = true;
            }
            return newMessages;
          });
          setIsGenerating(false);
          break;
          
        case 'error':
          console.error('Chat stream error:', event.data);
          setIsGenerating(false);
          // Add error message to chat
          setMessages(prev => [...prev, {
            id: Date.now().toString(),
            content: `Error: ${event.data}`,
            timestamp: new Date(),
            isUser: false,
            isComplete: true
          }]);
          break;
      }
    });

    wsService.onMemoryUpdate((memory) => {
      setProjectMemory(memory);
    });

    // Connect to WebSocket
    wsService.connect().catch(console.error);

    // Cleanup on unmount
    return () => {
      wsService.disconnect();
    };
  }, [wsService]);

  const sendMessage = useCallback((content: string) => {
    // Add user message immediately
    const userMessage: ChatMessage = {
      id: Date.now().toString(),
      content,
      timestamp: new Date(),
      isUser: true,
      isComplete: true
    };
    
    setMessages(prev => [...prev, userMessage]);
    
    // Send to WebSocket
    wsService.sendMessage(content);
  }, [wsService]);

  const stopGeneration = useCallback(() => {
    wsService.stopGeneration();
    setIsGenerating(false);
    
    // Mark last incomplete message as complete
    setMessages(prev => {
      const newMessages = [...prev];
      const lastMessage = newMessages[newMessages.length - 1];
      if (lastMessage && !lastMessage.isUser && !lastMessage.isComplete) {
        lastMessage.isComplete = true;
      }
      return newMessages;
    });
  }, [wsService]);

  const clearMessages = useCallback(() => {
    setMessages([]);
  }, []);

  const requestMemorySnapshot = useCallback(() => {
    wsService.requestMemorySnapshot();
  }, [wsService]);

  const value: ChatContextType = {
    messages,
    isConnected,
    isGenerating,
    connectionStatus,
    projectMemory,
    sendMessage,
    stopGeneration,
    clearMessages,
    requestMemorySnapshot
  };

  return <ChatContext.Provider value={value}>{children}</ChatContext.Provider>;
};

export const useChat = (): ChatContextType => {
  const context = useContext(ChatContext);
  if (context === undefined) {
    throw new Error('useChat must be used within a ChatProvider');
  }
  return context;
};
